import { Component } from '@angular/core';
import { ModalController,NavController, NavParams } from 'ionic-angular';

import {MessagesCommentsService} from "../../services/messages-comments";
import { UserService } from '../../services/user';

//page
import {SubcommentsPage} from "../subcomments/subcomments";

@Component({
  selector: 'page-message-comments',
  templateUrl: 'message-comments.html',
})
export class MessageCommentsPage {
  comments:any
  notice_id:number;
  notice_title:string;
  constructor(
    public navCtrl: NavController,
    public modalCtrl: ModalController,
    private messagesCommentsService:MessagesCommentsService,
    private userService: UserService,
    public navParams: NavParams
  ) {
    
    this.comments = []
    this.notice_id = this.navParams.get("notice_id")   
    this.notice_title = this.navParams.get("notice_title")   
    
  }

  ionViewDidLoad() {
    this.getComments()
  }
  getComments(){
    this.messagesCommentsService.get(this.notice_id).subscribe((data)=>{
      if(data){
        this.comments = data
      }
    })
  }

  subComments(comment:any){
    let modal = this.modalCtrl.create(SubcommentsPage,{
      comment:comment
    });
    // modal.onDidDismiss((categories)=>{
    //   // this.notice.categories = categories
    // });
    modal.present();
  }
}



