import { Component,NgZone } from '@angular/core';
import { ActionSheetController,Platform, NavController, NavParams,LoadingController,PopoverController,ModalController,ToastController } from 'ionic-angular';
import {DomSanitizer,SafeHtml} from '@angular/platform-browser';
import { Keyboard } from '@ionic-native/keyboard';

import { WindowRefService } from '../../services/windowref';
import { NewsService } from '../../services/news';

import {FontsizePage} from '../fontsize/fontsize';
import {FontFamilyPage} from '../font-family/font-family';
import {ParagraphFormatPage} from '../paragraph-format/paragraph-format';
import {AlignFormatPage} from '../align-format/align-format';
import {ReziseImagePage} from '../rezise-image/rezise-image';
import {LinkPage} from '../link/link';
import {VideoPage} from '../video/video';
import {CategoriesPage} from '../categories/categories';

//plugins
import { Camera,CameraOptions } from '@ionic-native/camera';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
