import firebase from "firebase"
import { SectionsAddPage } from '../sections-add/sections-add';
declare var cordova: any;

@Component({
  selector: 'page-wysiwyg',
  templateUrl: 'wysiwyg.html',
})
export class WysiwygPage {
  private _window: Window;
  cover_view:any;
  base64Image:any;
  editor:any;
  notice:{
    title:string,
    body:SafeHtml,
    categories:Array<any>,
    sections:Array<any>,
    privileges:Array<String>,
    publicationDate:Date,
    cover:string
  };
  imageChosen: any = 0;
  imagePath: any;
  imageNewPath: any;
  
  private notice_id=undefined
  private change_cover = false
  
  constructor(
    windowRef: WindowRefService,
    private newsService: NewsService,
    public actionSheetCtrl: ActionSheetController,
    public modalCtrl: ModalController,
    public navCtrl: NavController,
    public popoverCtrl: PopoverController,
    public loadingCtrl:LoadingController,
    public toastCtrl: ToastController,
    public navParams: NavParams,
    private sanitizer: DomSanitizer,
    private camera:Camera,
    private transfer: FileTransfer,
    private _zone: NgZone,
    private file: File,
    private keyboard: Keyboard,
    platform:Platform) {
    platform.ready().then(() => {
      this._window = windowRef.nativeWindow;
      this.editor = document.getElementById('editor')
      this.keyboard.show()
      this.cover_view=''
      this.notice_id = this.navParams.get("id")
      if(this.notice_id!=undefined){
        this.notice = {
          title:'',
          body:'',
          categories:[],
          sections:[],
          privileges:[],
          publicationDate: new Date(),
          cover:''
        };
        this.newsService.get(this.notice_id).subscribe(data => {
          if(data){
            console.log(data.publicationDate)
            this.notice_id = data.id
            this.notice.title = data.title
            this.notice.sections = [{
              title: data.seccion,
              checked:true
            }]
            this.notice.publicationDate = data.publicationDate
            this.notice.body = data.body.replace("covers/","covers%2F").replace("magueitoramp;","&")
            
            this.notice.cover = this.sanitizer.bypassSecurityTrustUrl("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers"+data.cover.replace("mague1","%252F").replace("mague2","?alt=").replace("mague3","&token="))['changingThisBreaksApplicationSecurity'].replace('covers/','covers%2F')
            document.getElementById("editor").innerHTML = data.body.replace("covers/","covers%2F").replace("magueitoramp;","&")
            // console.log(data)
            this.notice.privileges = data.privilegios.split(",")
            let tags = data.tags.split(",")
            for(var index=0;index<tags.length;index++){
              this.notice.categories.push({
                title:tags[index].trim(),
                checked:true
              })
            } 

            this.cover_view = this.notice.cover
            this.imageChosen = 1
            
          }
      });
      }else{
        this.notice = {
          title:'',
          body:'',
          categories:[{"title":"Huelva",checked:true}],
          sections:[{"title":"Noticias",checked:true}],
          privileges:["1","3"],
          publicationDate:new Date(),
          cover:''
        };
      }
    })
  }

  ionViewDidLoad() {
    // console.log('ionViewDidLoad WysiwygPage');
    document.getElementById("editor").focus()
  }

  bold(command) {
    document.execCommand(command, false, null);
  }
  link(){
    let popover = this.modalCtrl.create(LinkPage);
    let selectedElement = this._window.getSelection();
    let first = this._window.getSelection().anchorOffset;
    let range;
    // alert(typeof this._window.getSelection().getRangeAt(0))
    if(this._window.getSelection().getRangeAt(0)){
      range = this._window.getSelection().getRangeAt(0);
      let text = this._window.getSelection().toString()
      // alert(text)
      let ev = {
        target : {
          getBoundingClientRect : this.toPosition('link')
        }
      };
      popover.present(/*{ev}*/)
      popover.onDidDismiss((link)=>{
        if(link.length > 0 ){
          range.deleteContents();
          let element=document.createElement("a");
          element.href = link;
          element.innerHTML = text;
          // alert(element.toString())
          range.insertNode(element);
      }
      })
    }
  }
  insertVideo(){
    let self = this
    //this.keyboard.close()
    let popover = this.modalCtrl.create(VideoPage);
    let selectedElement = this._window.getSelection();
    let first = this._window.getSelection().anchorOffset;
    let range;
    // alert(typeof this._window.getSelection().getRangeAt(0))
    if(this._window.getSelection().getRangeAt(0)){
      range = this._window.getSelection().getRangeAt(0);
      let text = this._window.getSelection().toString()
      // alert(text)
      let ev = {
        target : {
          getBoundingClientRect : this.toPosition('insertVideo')
        }
      };
      popover.present(/*{ev}*/)
      popover.onDidDismiss((link)=>{
       if(link.length>0){
          range.deleteContents();
          let element=document.createElement("iframe");
          element.src = link;
          element["frameborder"] = 0;
          element["allowfullscreen"] = true;
          
          // element.innerHTML = text;
          // alert(element.toString())
          range.insertNode(element);
        }
      })
    }
  }
  fontSizePopover(){
    let popover = this.modalCtrl.create(FontsizePage);
    let selectedElement = this._window.getSelection()
    let range;
    let ev = {
      target : {
        getBoundingClientRect : this.toPosition('fontSize')
      }
    };
    popover.present({ev})
    popover.onDidDismiss((data)=>{
      if(selectedElement.focusNode!=null){
        range = this._window.getSelection().getRangeAt(0);
        let text = this._window.getSelection().toString()
        let formatblock = (selectedElement.anchorNode.parentNode.nodeName == "DIV")?'p':(selectedElement.anchorNode.parentNode.nodeName);
        //document.execCommand('formatblock', false, formatblock);
        let element = document.createElement(formatblock)
        element.innerText = text
        //let selectedElementEdit = selectedElement.focusNode.parentNode;
        element['style'].fontSize = data+'px';
        range.deleteContents()
        range.insertNode(element)
      }
    })
  }
  fontFamilyPopover(){
    let popover = this.modalCtrl.create(FontFamilyPage)
    let selectedElement = this._window.getSelection()
    let range;
    let ev = {
      target : {
        getBoundingClientRect : this.toPosition('fontFamily')
      }
    };
    popover.present(/*{ev}*/)
    popover.onDidDismiss((data)=>{
      if(this._window.getSelection().focusNode!=null){
        if(selectedElement.focusNode!=null){
          range = this._window.getSelection().getRangeAt(0)
          let formatblock = (selectedElement.anchorNode.parentNode.nodeName == "DIV")?'p':(selectedElement.anchorNode.parentNode.nodeName);
          let element = selectedElement
          //element.innerHTML = selectedElement.anchorNode.parentElement.innerHTML
          //let selectedElementEdit = selectedElement.focusNode.parentNode;
          element.anchorNode.parentNode['style'].fontFamily = data;
          //range.deleteContents()
          //range.insertNode(element.anchorNode)
        }
        
        //document.execCommand('formatblock', false, formatblock);
        //let element = document.createElement(formatblock)
        
      }
    })
  }

  paragraphFormatPopover(){
    let popover = this.modalCtrl.create(ParagraphFormatPage)
    let selectedElement = this._window.getSelection()
    let range;
    let ev = {
      target : {
        getBoundingClientRect : this.toPosition('paragraphFormat')
      }
    };
    popover.present({ev})
    popover.onDidDismiss((data)=>{
      if(selectedElement.focusNode!=null){
        //document.execCommand('formatblock', false, data);
        if(selectedElement.focusNode!=null){
          range = this._window.getSelection().getRangeAt(0)
          let formatblock = (selectedElement.anchorNode.parentNode.nodeName == "DIV")?'p':(selectedElement.anchorNode.parentNode.nodeName);
          let element = selectedElement
          //element.innerHTML = selectedElement.anchorNode.parentElement.innerHTML
          //let selectedElementEdit = selectedElement.focusNode.parentNode;
          if(data=="H1"){
            element.anchorNode.parentNode['style']["font-size"] = "2em";
            element.anchorNode.parentNode['style']["font-weight"] = "bold";
          }else if(data=="H2"){
            element.anchorNode.parentNode['style']["font-size"] = "1.5em";
            element.anchorNode.parentNode['style']["font-weight"] = "bold";
          }else if(data=="H3"){
            element.anchorNode.parentNode['style']["font-size"] = "1.17em";
            element.anchorNode.parentNode['style']["font-weight"] = "bold";
          }else if(data=="H4"){
            element.anchorNode.parentNode['style']["font-size"] = "1em";
            element.anchorNode.parentNode['style']["font-weight"] = "bold";
          }else if(data=="H5"){
            element.anchorNode.parentNode['style']["font-size"] = "0.83em";
            element.anchorNode.parentNode['style']["font-weight"] = "bold";
          }else if(data=="H6"){
            element.anchorNode.parentNode['style']["font-size"] = "0.67em";
            element.anchorNode.parentNode['style']["font-weight"] = "bold";
          }else if(data=="pre"){
            element.anchorNode.parentNode['style']["white-space"] = "pre";
            element.anchorNode.parentNode['style']["font-family"] = "monospace";
            element.anchorNode.parentNode['style']["font-weight"] = "normal";
          }else{
            element.anchorNode.parentNode['style']["white-space"] = "";
            element.anchorNode.parentNode['style']["font-family"] = "";
            element.anchorNode.parentNode['style']["font-size"] = "1em";
            element.anchorNode.parentNode['style']["font-weight"] = "normal";
          }
          
          //range.deleteContents()
          //range.insertNode(element.anchorNode)
        }
      }
    })
  }
  alignPopover(){
    let popover = this.popoverCtrl.create(AlignFormatPage)
    let ev = {
      target : {
        getBoundingClientRect : this.toPosition('align')
      }
    };
    popover.present(/*{ev}*/);
    let selectedElement = this._window.getSelection()

    popover.onDidDismiss((align)=>{
      if(selectedElement.focusNode!=null){
        // let formatblock = (selectedElement.anchorNode.parentNode.nodeName == "DIV")?'p':(selectedElement.anchorNode.parentNode.nodeName);
        // document.execCommand('formatblock', false, formatblock);
        let selectedElementEdit = selectedElement.focusNode.parentNode;
        selectedElementEdit['style'].textAlign = align;
      }
    })
  }
  align(align){
    let selectedElement = this._window.getSelection()
    // console.log(selectedElement.anchorNode.parentNode)
    // a.anchorNode.parentNode.innerText
    if(selectedElement.anchorNode.parentNode.nodeName == "SPAN"){
      let oldElement:any;
      oldElement = selectedElement.anchorNode.parentElement
      oldElement.replaceWith(oldElement.innerText)
    };

    let selectedElementEdit = selectedElement.focusNode.parentNode;
    selectedElementEdit['style'].textAlign = align;
    // document.execCommand('formatblock', false, align)
  }
  setCategories(){
    let modal = this.modalCtrl.create(CategoriesPage,{
      categories:this.notice.categories
    });
    modal.onDidDismiss((categories)=>{
      this.notice.categories = categories
    });
    modal.present();
  }
  setSections(){
    let modal = this.modalCtrl.create(SectionsAddPage,{
      sections:this.notice.sections
    });
    modal.onDidDismiss((sections)=>{
      console.log("Seccion resultado:",sections)
      this.notice.sections = sections
    });
    modal.present();
  }
  getImage(){
    // const options: CameraOptions = {
    //   destinationType: this.camera.DestinationType.DATA_URL,
    //   encodingType: this.camera.EncodingType.JPEG,
    //   mediaType: this.camera.MediaType.PICTURE
    // }
    this.keyboard.close()
    let modal = this.modalCtrl.create(ReziseImagePage);
    let selectedElement = this._window.getSelection();
    let first = this._window.getSelection().anchorOffset;
    let range = undefined;
    if(this._window.getSelection().getRangeAt(0)){
      range =this._window.getSelection().getRangeAt(0);
      let element=document.createElement("img");
      // element.setAttribute("src","data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAHElEQVQI12P4//8/w38GIAXDIBKE0DHxgljNBAAO9TXL0Y4OHwAAAABJRU5ErkJggg==");
      // range.insertNode(element)
      modal.onDidDismiss((img)=>{
        if(img!=null && img.length>0){
          
          let storageRef = firebase.storage().ref();
          const filename = Math.floor(Date.now()/1000);
          const imageRef = storageRef.child(`covers/${filename}.jpg`)
          let base64 ="data:image/jpeg;base64,"+img
          imageRef.putString(base64,firebase.storage.StringFormat.DATA_URL).then((snapshot)=> {
            let image_url = snapshot.downloadURL
            let image_url2 = image_url.split("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers")[1]
            image_url2 = image_url2.replace("%252F","mague1").replace("?alt=","mague2").replace("&token=","mague3")
            let element=document.createElement("img");
            element.setAttribute("src",image_url);
            range.insertNode(element)
           },(error)=>{
             console.log("Errorcito: ",error)
           })
            
        }
      })
      modal.present()
    }
  }
  insertOrderedList(){
    document.execCommand("insertOrderedList",false,null)
  }
  insertUnorderedList(){
    document.execCommand("insertUnorderedList",false,null)
  }
  indent(){
    document.execCommand("indent",false,null)
  }
  outdent(){
    document.execCommand("outdent",false,null)
  }
  redo(){
    document.execCommand("redo",false,null)
  }
  undo(){
    document.execCommand("undo",false,null)
  }
  strikeThrough(){
    document.execCommand("strikeThrough",false,null)
  }

  //utils
  toPosition(element:String){
    return ()=>{
      return {
      top: document.getElementById(''+element).getBoundingClientRect().top,
      left: document.getElementById(''+element).getBoundingClientRect().left
    }};
  }

  add(){
    // this.notice.body = document.getElementById("editor").innerHTML
    
    // console.log(body)
    let ulti_categories = []
    for(var index = 0;index<this.notice.categories.length;index++){
      ulti_categories.push(this.notice.categories[index].title)
      if(index == (this.notice.categories.length - 1)){
        let final_notice:{
          title:string,
          categories:Array<String>,
          section:string,
          privileges:Array<String>,
          cover:string,
          update:boolean
          publicationDate:Date,
          id_notice:string
        };
        let body :String;
        final_notice={
          title:this.notice.title,
          categories:ulti_categories,
          section:this.notice.sections[0].title,
          privileges:this.notice.privileges,
          cover:this.notice.cover,
          update:(this.notice_id != undefined)?true:false,
          publicationDate:this.notice.publicationDate,
          id_notice:(this.notice_id != undefined)?this.notice_id:''
        }
        body = document.getElementById("editor").innerHTML.replace("&","magueitor");
        
        let loader = this.loadingCtrl.create({
          content:"Publicando noticia por favor espere"
        })
        loader.present()
        this.newsService.add(this.sanitizer.bypassSecurityTrustHtml('data='+ JSON.stringify(final_notice)+"&body="+body)['changingThisBreaksApplicationSecurity']).subscribe(data => {
          // console.log(JSON.stringify(data))
          if(data.success){
            this.notice = {
              title:'',
              body:'',
              categories:[],
              sections:[],
              cover:'',
              publicationDate:null,
              privileges:[]
            };
            document.getElementById("editor").innerHTML = ''
            loader.dismiss()
            let toast = this.toastCtrl.create({
              message: data.success,
              duration: 2000
            });
            toast.present();
            this.navCtrl.pop();
            
          }else if(data.error){
            loader.dismiss()
          }
        },(error)=>{
          console.log(error)
          loader.dismiss()
        })
      }
    }
    
  }
  getContent(event:any){
    
    var map = {
      '&': '&amp;',
      '<': '&lt;',
      '>': '&gt;',
      '"': '&quot;',
      "'": '&#039;'
    };
  
    this.notice.body = this.sanitizer.bypassSecurityTrustHtml(event.target.innerHTML)['changingThisBreaksApplicationSecurity']
    // this.notice.body = this.sanitizer.bypassSecurityTrustHtml(event.target.innerHTML.replace(/[&<>"']/g, function(m) { return map[m]; }))['changingThisBreaksApplicationSecurity']
    // console.log(this.notice.body)
  }

  chooseImageCover() {
    let options = {
      quality: 100,
      destinationType:this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      allowEdit: false,
      encodingType: this.camera.EncodingType.JPEG,
      targetWidth: 500,
      targetHeight: 500,
      saveToPhotoAlbum: false
    }
    this.camera.getPicture(options).then((imgUrl) => {
      this.change_cover = true
      this.cover_view = "data:image/jpeg;base64,"+imgUrl;
      this.cover_view = this.sanitizer.bypassSecurityTrustUrl(this.cover_view)
      this.base64Image ="data:image/jpeg;base64,"+imgUrl
      this.notice.cover = imgUrl;
      this.imageChosen = 1;
      /*this.file.copyFile(sourceDirectory, sourceFileName, cordova.file.externalApplicationStorageDirectory, sourceFileName).then((result: any) => {
        this._zone.runOutsideAngular(() => {
          this._zone.run(() => {
            this.imagePath = imgUrl;
            // alert(this.imagePath)
          });
        })

        this.imageChosen = 1;
        this.imageNewPath = result.nativeURL;
        // alert(JSON.stringify(result))
      }, (err) => {
        alert(JSON.stringify(err));
      })*/

    }, (err) => {
      //alert(JSON.stringify(err))
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Elija una imagen',
      buttons: [
        {
          text: 'Galeria',
          icon: 'albums',
          handler: () => {
            this.actionHandler(1);
          }
        },
        {
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });

    //actionSheet.present();
  }
  actionHandler(selection: any) {
    var options: any;

    if (selection == 1) {
      console.log("Opcion 1");
      
      options = {
        quality: 100,
        destinationType:this.camera.DestinationType.DATA_URL,
        sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
        allowEdit: false,
        encodingType: this.camera.EncodingType.JPEG,
        targetWidth: 500,
        targetHeight: 500,
        saveToPhotoAlbum: false
      };
    } else {
      options = {
        quality: 100,
        destinationType: this.camera.DestinationType.FILE_URI,
        sourceType: this.camera.PictureSourceType.CAMERA,
        allowEdit: false,
        encodingType: this.camera.EncodingType.JPEG,
        targetWidth: 500,
        targetHeight: 500,
        saveToPhotoAlbum: false
      };
    }
    //document.getElementById("title").focus()
    setTimeout(function() {
      this.camera.getPicture(options).then((imgUrl) => {
        this.change_cover = true
        this.cover_view = "data:image/jpeg;base64,"+imgUrl;
        this.cover_view = this.sanitizer.bypassSecurityTrustUrl(this.cover_view)
        this.base64Image ="data:image/jpeg;base64,"+imgUrl
        this.notice.cover = imgUrl;
        this.imageChosen = 1;
        /*this.file.copyFile(sourceDirectory, sourceFileName, cordova.file.externalApplicationStorageDirectory, sourceFileName).then((result: any) => {
          this._zone.runOutsideAngular(() => {
            this._zone.run(() => {
              this.imagePath = imgUrl;
              // alert(this.imagePath)
            });
          })
  
          this.imageChosen = 1;
          this.imageNewPath = result.nativeURL;
          // alert(JSON.stringify(result))
        }, (err) => {
          alert(JSON.stringify(err));
        })*/
  
      }, (err) => {
        //alert(JSON.stringify(err))
      });
    }, 0);
    

  }
  
  upload(){
    if(this.notice.title.length < 1){
      let popover = this.toastCtrl.create({
        message:"Titulo requerido",
        duration:3000
      })
      popover.present()
      return;
    }
    if(this.notice.cover.length < 1){
      let popover = this.toastCtrl.create({
        message:"Imagen de portada requerida",
        duration:3000
      })
      popover.present()
      return;
    }
    if(document.getElementById("editor").innerHTML.trim().length < 1){
      let popover = this.toastCtrl.create({
        message:"Contenido requerido",
        duration:3000
      })
      popover.present()
      return;
    }
    if(this.notice.categories.length < 1){
      let popover = this.toastCtrl.create({
        message:"Al menos una categoria requerida",
        duration:3000
      })
      popover.present()
      return;
    }
    if(this.notice.privileges.length < 1){
      let popover = this.toastCtrl.create({
        message:"Al menos tipo de usuario requerido",
        duration:3000
      })
      popover.present()
      return;
    }
    // console.log("Paso")
    if(this.change_cover == false){
      // console.log(this.notice.cover)
      this.notice.cover = this.notice.cover.split("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers")[0]
      this.notice.cover = this.notice.cover.replace("%252F","mague1").replace("?alt=","mague2").replace("&token=","mague3")
      
      this.add()
    }else{
      let storageRef = firebase.storage().ref();
      const filename = Math.floor(Date.now()/1000);
      const imageRef = storageRef.child(`covers/${filename}.jpg`)
      imageRef.putString(this.base64Image,firebase.storage.StringFormat.DATA_URL).then((snapshot)=> {
        this.notice.cover = snapshot.downloadURL
        this.notice.cover = this.notice.cover.split("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers")[1]
        this.notice.cover = this.notice.cover.replace("%252F","mague1").replace("?alt=","mague2").replace("&token=","mague3")
        
        this.add()
       },(error)=>{
         console.log("Errorcito: ",error)
       })
    }
  }
 
}



