import { Component  } from '@angular/core';

import { Events,Platform, ActionSheetController,ToastController,NavController,NavParams, AlertController,LoadingController, MenuController } from 'ionic-angular';
import { Http, Headers,RequestOptions } from '@angular/http';
import {DomSanitizer,SafeHtml} from '@angular/platform-browser';

//Providers
import {NewsService} from '../../services/news';
import {UserService} from '../../services/user';

import {DetailPage} from '../detail/detail';
import {WysiwygPage} from '../wysiwyg/wysiwyg';
// import { AppPreferences } from '@ionic-native/app-preferences';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  public show_search = false
  public edit_id = undefined
  public selection = "no_leidas";
  userData :any;
  http: Http;
  ip: any;
  domActive: false;
  loadingCtrl: LoadingController;
  selectedItem: any;
  noticias: Array<{
    id:number,
    title:string,
    body:string,
    cover:string,
    autor:string,
    date:string,
    tags:Array<{
      id:number,
      nombre:string
    }>
  }>;
  
  categoria:number
  title:string
  loader:any
  constructor(
    public navCtrl: NavController,
    public actionSheetCtrl:ActionSheetController,
    public platform: Platform,
    public newsService: NewsService,
    public userService: UserService,
    public alertCtrl: AlertController,
    private toastCtrl:ToastController,
    public events : Events,
    public menu : MenuController,
    public navParams: NavParams,
    private sanitizer: DomSanitizer,
    // private appPreferences: AppPreferences,
    http: Http,
    loadingCtrl: LoadingController) {
      platform.ready().then(() => {
        this.loadingCtrl = undefined
        this.loadingCtrl = loadingCtrl
  
        this.userData = this.userService.getData()
        console.log(this.userData)
        this.selectedItem = navParams.get('item');
  
        this.noticias = [];
        
        this.categoria = (this.navParams.get("id"))?this.navParams.get("id"):undefined
        if(this.categoria == undefined){
          this.title = "Inicio"
        }else{
          this.title = "Categoria - "+this.navParams.get('name')
        }
        this.getNewsSimple(0,this.categoria)
        
      });
      
      /*this.appPreferences.fetch('user').then((res)=>{
        // this.userService.addUserData(data);
        // this.navCtrl.setRoot(HomePage);

        if((typeof res) == "object"){

        }else{
          this.userService.deleteData()
          this.appPreferences.remove('user')
          this.navCtrl.setRoot(HomePage)
        }

      })*/
      // let loader = this.loadingCtrl.create({
      //   content:"Obteniendo noticias desde el servidor por favor espere",
      //   duration:3000
      // })
      // loader.present();
    }
  itemTapped(event, noticia:any) {
    if(this.edit_id == undefined){
      this.navCtrl.push(DetailPage, {
        id: noticia.id
      });
    }
  }
  getNewsSimple(limit:number,category:number){
    let vistas = (this.selection == "no_leidas")?0:1;
    this.newsService.getNewsSimple(limit,category,vistas).subscribe(data => {
        // this.loader.dismiss()
        if(data){
          this.noticias = undefined
          this.noticias = []
          console.log("Con data",data)
          console.log("Noticias",this.noticias)
          for(var i = 0;i<data.length;i++){
            // console.log(this.sanitizer.bypassSecurityTrustUrl("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers"+data[i].cover.replace("mague1","%2F15").replace("mague2","?alt=").replace("mague3","&token="))['changingThisBreaksApplicationSecurity'].replace('covers/','covers%2F'))
            this.noticias.push({
              id:data[i].id,
              title:data[i].title,
              body:data[i].body,
              cover:this.sanitizer.bypassSecurityTrustUrl("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers"+data[i].cover.replace("mague1","%2F").replace("mague2","?alt=").replace("mague3","&token="))['changingThisBreaksApplicationSecurity'].replace('covers/','covers%2F'),
              autor:data[i].autor,
              date:data[i].date,
              tags:data[i].tags
              });
          }
        }else{
          console.log("No data")
          this.noticias = null
          this.noticias = undefined
          this.noticias = []
        }
    });
  }
  getNews(limit:number,category:number,infiniteScroll){
    let vistas = (this.selection == "no_leidas")?0:1;
    this.loader = this.loadingCtrl.create({
      content:"Obteniendo noticias desde el servidor por favor espere"
    })
    this.loader.present();
   this.newsService.getNews(limit,category,vistas).subscribe(data => {
        if(data){
          if(data.length ==0){
            this.loader.dismiss()
            infiniteScroll.complete()
          }else{
            this.loader.dismiss()
            // "id":,"title","body","date":,"autor","premium","cover"}
            for(var i = 0;i<data.length;i++){
              this.noticias.push({
                id:data[i].id,
                title:data[i].title,
                body:data[i].body,
                cover:this.sanitizer.bypassSecurityTrustUrl("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers"+data[i].cover.replace("mague1","%252F").replace("mague2","?alt=").replace("mague3","&token="))['changingThisBreaksApplicationSecurity'].replace('covers/','covers%2F'),
                autor:data[i].autor,
                date:data[i].date,
                tags:data[i].tags
              });
              if(i==data.length-1){
                infiniteScroll.complete()
              }
            }
          }
          // this.userService.addUserData(data.data);
          //alert(JSON.stringify(this.userService.getData()));
        }else{
          this.loader.dismiss()
          infiniteScroll.complete()
        }
    });
  }
  presentLoading(){
    let loader = this.loadingCtrl.create({
      content:"Obteniendo noticias desde el servidor por favor espere",
      duration:3000
    })
    loader.present();
  }
  ionViewDidLoad() {
    this.userData = this.userService.getData();
    this.events.subscribe('menu:opened', () => {

    });

    this.events.subscribe('menu:closed', () => {

    });
    //alert(JSON.stringify(this.userData));
  }
  refresh(){
    this.noticias = []
    this.categoria = (this.navParams.get("id"))?this.navParams.get("id"):undefined
    this.getNewsSimple(0,this.categoria)
  }
  ionViewWillEnter(){
    console.log("volvio")
  }
  doInfinite(infiniteScroll) {
    console.log('Begin async operation');
    this.getNews(this.noticias.length,this.categoria,infiniteScroll);
  }
  searchToggle(){
    if(this.show_search){
      this.show_search = false
    }else{
      this.show_search = true
    }
  }
  filterItems(ev:any){
    let val = ev.target.value

    if(val && val.trim() != ''){
      this.noticias = this.noticias.filter((noticia)=>{
        if(noticia.title.toLowerCase().indexOf(val.toLowerCase())>-1){
          return noticia
        }else{
          console.log("No hay coincidencias")
        }
      })
    }
  }

  edit(id:any){
    this.edit_id = id
    this.navCtrl.push(WysiwygPage, {
      id: id,
      edit:true
    });
    this.edit_id = undefined
  }
  trash(id:any){
    this.edit_id = id
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Esta seguro de eliminar la noticia?',
      buttons: [
        {
          text: 'Confirmar',
          icon: 'trash',
          handler: () => {
            this.newsService.trash(this.edit_id).subscribe((data)=>{
              this.edit_id = undefined
              console.log(data)
              if(data.success ==""){
                this.noticias = []
                this.categoria = (this.navParams.get("id"))?this.navParams.get("id"):undefined
                this.getNewsSimple(0,this.categoria)
              }else{
                let toast = this.toastCtrl.create({
                  message: 'Error al elinar la noticia',
                  duration: 3000
                });
                toast.present();
              }
            })
          }
        },
        {
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
            this.edit_id = undefined
          }
        }
      ]
    });

    actionSheet.present();
  }

  changeTab(val){
		if(val == 0){
      this.selection = "no_leidas";
      this.noticias = null;
      this.noticias = undefined;
      this.noticias = [];
      this.categoria = (this.navParams.get("id"))?this.navParams.get("id"):undefined
      if(this.categoria == undefined){
        this.title = "Inicio"
      }else{
        this.title = "Categoria - "+this.navParams.get('name')
      }
      this.getNewsSimple(0,this.categoria)
		}
		else{
      this.selection = "leidas";
      this.noticias = null;
      this.noticias = undefined;
      this.noticias = [];
      
      this.categoria = (this.navParams.get("id"))?this.navParams.get("id"):undefined
      if(this.categoria == undefined){
        this.title = "Inicio"
      }else{
        this.title = "Categoria - "+this.navParams.get('name')
      }
      this.getNewsSimple(0,this.categoria)
		}
	}
}



