import { Component } from '@angular/core';
import { NavController, NavParams,ViewController } from 'ionic-angular';

@Component({
  selector: 'page-fontsize',
  templateUrl: 'fontsize.html',
})
export class FontsizePage {
  sizes:Array<Number>
  constructor(
    public navCtrl: NavController,
    public viewCtrl: ViewController,
    public navParams: NavParams) {
    this.sizes = []
    for(var i = 8;i<=96;i++){
      if(i<=12){
        this.sizes.push(i)
      }else{
        i+=1
        this.sizes.push(i)
      }
    }
  }

  ionViewDidLoad() {
    let element = document.getElementById("fontsize")
    let parent=element.parentElement
    let parent2 = parent.parentElement
    //parent2.parentElement.style['width'] = "92px"
    // parent2.parentElement.style['height'] = "fit-content"
  }


  setSize(size){
    this.viewCtrl.dismiss(size)
  }
}



