import { Component } from '@angular/core';
import { ActionSheetController ,NavController, NavParams } from 'ionic-angular';

//Providers
import {UsersPermisionsService} from '../../services/permisions';
@Component({
  selector: 'page-users',
  templateUrl: 'users.html',
})
export class UsersPage {
  users:Array<{
    id:number,
    permision:number,
    email:string,
    nombre:string,
    usuario:string,
    privilegio:string
  }>
  constructor(
    public navCtrl: NavController,
    private usersPermisionsService:UsersPermisionsService,
    public actionSheetCtrl:ActionSheetController,
    public navParams: NavParams
  ) {
    this.users = []
  }

  ionViewDidLoad() {
    this.all()
  }

  all(){
    this.users = []
    this.usersPermisionsService.all().subscribe((data)=>{
      console.log(data)
      if(data.usuarios){
        data.usuarios.forEach(user => {
          this.users.push(user)
        });
      }
    })
  }
  permisos(user:any) {
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Privilegios',
      buttons: [
        {
          text: 'Normal',
          role: 'normal',
          handler: () => {
            console.log('Destructive clicked');
            console.log(user)
            if(user.admin == 1){
              this.update(user.id,3)
            }
          }
        },{
          text: 'Administrador',
          handler: () => {
            console.log('Archive clicked');
            console.log(user)
            if(user.admin == 3){
              this.update(user.id,1)
            }
          }
        },{
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  update(id:number,permise:number){
    console.log(id+"-"+permise)
    this.usersPermisionsService.update({usuario:id,admin:permise}).subscribe((data)=>{
      if(data){
        this.all()
      }
    })
  }
}



