import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import {MessagesService} from "../../services/messages";
import { UserService } from '../../services/user';


@Component({
  selector: 'page-message',
  templateUrl: 'message.html',
})
export class MessagePage {
  message:{
    _id?: string;
    chatId?: string;
    text?: string;
    user?: string;
    createdAt?: Date;
  }
  messages:any
  admin:any
  user_id:number
  chatBox:string
  public chatData = {text:'',user:'',chatId:''};
  constructor(
    public navCtrl: NavController,
    private messagesService:MessagesService,
    private userService: UserService,
    public navParams: NavParams
  ) {
    this.admin = this.navParams.get('user');
    console.log(this.admin.id)
    this.user_id = this.userService.getData().user.userdata.id;
    console.log("UserData: ",this.userService.getData().user.userdata.id)
    this.chatData.user =this.user_id+''
    this.chatBox = ""
		
    this.messages = []
    
    
  }

  ionViewDidLoad() {
    this.getMessages()
  }
  getMessages(){
    this.messages = []
    if(this.userService.getData().user.userdata.admin == 3){
      this.messagesService.get(this.user_id,this.admin.id).subscribe((data)=>{
        if(data){
          data.forEach(element => {
            this.messages.push({
              // _id:"1",
              send:element.envio,
              text:element.content,
              user:element.usuario,
              createdAt:new Date(element.fecha)
            })
          });
        }
      })
    }else if(this.userService.getData().user.userdata.admin == 1){
      this.messagesService.get(this.admin.id,this.user_id).subscribe((data)=>{
        if(data){
          data.forEach(element => {
            this.messages.push({
              // _id:"1",
              send:element.envio,
              text:element.content,
              user:element.usuario,
              createdAt:new Date(element.fecha)
            })
          });
        }
      })
      
    }
  }
  send(message:string){
    if(this.userService.getData().user.userdata.admin == 3){
      this.messagesService.add(this.user_id,this.admin.id,message).subscribe((data)=>{
        if(data.status == "ok"){
          this.chatBox = ""
          
          this.getMessages()
        }
      })
    }if(this.userService.getData().user.userdata.admin == 1){
      this.messagesService.add(this.admin.id,this.user_id,message).subscribe((data)=>{
        if(data.status == "ok"){
          this.chatBox = ""
          
          this.getMessages()
        }
      })
    }
  }
}



