import { Component } from '@angular/core';
import { NavController,ViewController, NavParams } from 'ionic-angular';

import {NewsService} from "../../services/news";
@Component({
  selector: 'page-sections-add',
  templateUrl: 'sections-add.html',
})
export class SectionsAddPage {
  categories:Array<{
    title:string,
    checked:boolean
  }>
  categories_value:string
  category_selected:any
  public old_categories_value:Array<string>= []
  constructor(
    public navCtrl: NavController,
    public viewCtrl: ViewController,
    public newsService:NewsService,
    public navParams: NavParams
    ) {
      this.categories = []
      this.category_selected = []
      this.newsService.sections().subscribe((data)=>{
        // console.log(data)
        for (let index = 0; index < data.length; index++) {
          let el = data[index].nombre;
          // console.log(el)
          if (this.old_categories_value.indexOf(el.trim())==-1){
            this.categories.push({
              title:el.trim(),
              checked:false
            });
            this.old_categories_value.push(el.trim())
            // console.log(this.old_categories_value)
          }
          
        }
      })
      
      // this.categories = this.categories_value.split(",")
  }

  ionViewDidLoad() {
    // let element = document.querySelector("#categories>.scroll-content")
    // element['style']['padding-bottom'] = "0!important"
    // element['style']['position'] = 'absolute'
    // element['style']['display'] = 'block'
    // element['style']['margin'] = 0
    let element = document.querySelector(".modal-wrapper")
    element['style']['margin'] = 0
    element['style']['top'] = 0
    element['style']['left'] = 0
    element['style']['width'] = '100%'
    element['style']['height'] = '100%'
    
  }
  getCategories(){
    if(this.categories_value.length < 1){
      return;
    }
    let nuevas = this.categories_value.split(",")
    let nuevas_ok = []
    for (let index = 0; index < nuevas.length; index++) {
      let el = nuevas[index];
      // console.log(this.old_categories_value.indexOf(el.trim()))
      if (this.old_categories_value.indexOf(el)==-1){
        nuevas_ok.push(el.trim());
        this.old_categories_value.push(el.trim())
      }
      
      if(index == nuevas.length-1){
        for (let index2 = 0; index2 < nuevas_ok.length; index2++) {
          this.categories.push({
            title:nuevas_ok[index2],
            checked:false
          })
        }
      }
    }
    this.categories_value = ""
    // this.categories = ;
  }
  updateCategories(category:any){
    console.log(category);
    
    this.category_selected = [category]
  }
  add(){

    this.viewCtrl.dismiss(this.category_selected)
  }
}



