import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule } from '@angular/http';
//Providers
import {UserService} from '../services/user';
import {RegisterService} from '../services/register';
import {LoginService} from '../services/login';
import {NewsService} from '../services/news';
import {UsersPermisionsService} from '../services/permisions';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { WelcomePage } from '../pages/welcome/welcome';
import { LoginPage } from '../pages/login/login';
import { RegisterPage } from '../pages/register/register';
import {DetailPage} from '../pages/detail/detail';
import {UsersPage} from '../pages/users/users';

import { WindowRefService } from '../services/windowref';

//Wysiwyg and components
import { WysiwygPage } from '../pages/wysiwyg/wysiwyg';
import {FontsizePage} from '../pages/fontsize/fontsize';
import {FontFamilyPage} from '../pages/font-family/font-family';
import {ParagraphFormatPage} from '../pages/paragraph-format/paragraph-format';
import {AlignFormatPage} from '../pages/align-format/align-format';
import {ReziseImagePage} from '../pages/rezise-image/rezise-image';
import {LinkPage} from '../pages/link/link';
import {VideoPage} from '../pages/video/video';
import {CategoriesPage} from '../pages/categories/categories';
import {UsersChatPage} from '../pages/users-chat/users-chat';
import {CommentsChatPage} from '../pages/comments-chat/comments-chat';
import {SubcommentsPage} from '../pages/subcomments/subcomments';
import {MessagePage} from '../pages/message/message';
import {MessageCommentsPage} from '../pages/message-comments/message-comments';
import {SectionsPage} from "../pages/sections/sections"
import {SectionsDetailPage} from "../pages/sections-detail/sections-detail"

//calendar
import {CalendarPage} from '../pages/calendar/calendar';
//Plugins
import { Camera } from '@ionic-native/camera';
import { FileTransfer } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
//import {Push} from "@ionic-native/push";
import { FCM } from '@ionic-native/fcm';

import { AppPreferences } from '@ionic-native/app-preferences';
import {CalendarModule} from 'angular-calendar';
import { MessagesService } from '../services/messages';
import { MessagesCommentsService } from '../services/messages-comments';
import { CommentsService } from '../services/comments';
import { SectionsAddPage } from '../pages/sections-add/sections-add';

import { Keyboard } from '@ionic-native/keyboard';
@NgModule({
  declarations: [
    MyApp,
    HomePage,
    WelcomePage,
    LoginPage,
    RegisterPage,
    DetailPage,
    WysiwygPage,
    FontsizePage,
    FontFamilyPage,
    ParagraphFormatPage,
    AlignFormatPage,
    ReziseImagePage,
    LinkPage,
    VideoPage,
    CalendarPage,
    CategoriesPage,
    UsersPage,
    UsersChatPage,
    MessagePage,
    MessageCommentsPage,
    CommentsChatPage,
    SubcommentsPage,
    SectionsPage,
    SectionsAddPage
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    IonicModule.forRoot(MyApp,{
      scrollPadding: false,
      scrollAssist: false,
      autoFocusAssist: false,
      platforms: {
				ios: {
					backButtonText: ''
				}
			},
      monthNames: ['Enero', 'Febrero', 'Marzo', "Abril", "Mayo", "Junio", "Julio","Agosto","Septiembre","Octubre","Noviembre","Diciembre" ],
      monthShortNames: ['Ene', 'Feb', 'Mar',  "Abril", "May", "Jun", "Jul","Agos","Sep","Oct","Nov","Dic" ],
      dayNames: ['Domingo', 'Lunes', "Martes", "Miercoles", "Jueves", "Viernes", "Sabado"],
      dayShortNames: ['Dom', 'Lun', "Mar", "Mier", "Jue", "Vie", "Sab"],
    }, 
{
  links: [
    { loadChildren: '../pages/sections-detail/sections-detail.module#SectionsDetailPageModule', name: 'SectionsDetailPage', segment: 'sections-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module#TabsPageModule', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] }
  ]
}),
    CalendarModule.forRoot(),
    HttpModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    WelcomePage,
    LoginPage,
    RegisterPage,
    DetailPage,
    WysiwygPage,
    FontsizePage,
    FontFamilyPage,
    ParagraphFormatPage,
    AlignFormatPage,
    ReziseImagePage,
    LinkPage,
    VideoPage,
    CalendarPage,
    CategoriesPage,
    UsersPage,
    UsersChatPage,
    MessagePage,
    MessageCommentsPage,
    CommentsChatPage,
    SubcommentsPage,
    SectionsPage,
    SectionsAddPage,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    UserService,
    UsersPermisionsService,
    RegisterService,
    LoginService,
    NewsService,
    WindowRefService,
    MessagesService,
    CommentsService,
    MessagesCommentsService,
    Camera,
    Keyboard,
    File,
    FileTransfer,
    //Push,
    FCM,
    AppPreferences,
    // {provide: Window, useValue: window },
    {provide: ErrorHandler, useClass: IonicErrorHandler},
  ]
})
export class AppModule {}



