import { Component, TemplateRef, ViewChild } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import {CalendarEvent,CalendarMonthViewDay,DAYS_OF_WEEK} from 'angular-calendar';
import {
  isSameMonth,
  isSameDay,
  startOfMonth,
  endOfMonth,
  startOfWeek,
  endOfWeek,
  startOfDay,
  endOfDay,
  format
} from 'date-fns';

import 'rxjs/add/operator/map';
import { Observable } from 'rxjs/Observable';
//Servicios
import {NewsService} from '../../services/news';
//Pages
import {DetailPage} from '../detail/detail'
interface Notice {
  id: number;
  title: string;
  date: string;
}
@Component({
  selector: 'page-calendar',
  templateUrl: 'calendar.html',
})
export class CalendarPage {
  events$: Observable<Array<CalendarEvent<{ notice: Notice }>>>;
  public date: Date = new Date(Date.now());
  public row_show_header = false
  ano:string;
  mes:string;
  public colors: any = {
    red: {
      primary: '#ad2121',
      secondary: '#FAE3E3'
    },
    blue: {
      primary: '#1e90ff',
      secondary: '#D1E8FF'
    },
    yellow: {
      primary: '#e3bc08',
      secondary: '#FDF1BA'
    }
  }
  days_label: Array<string> = [
    "L", "M", "X", "J", "V", "S", "D"
  ];
  view: string = 'month';
  viewDate: Date = new Date();
  locale: string = 'es';
  weekStartsOn: number = DAYS_OF_WEEK.MONDAY;
  weekendDays: number[] = [DAYS_OF_WEEK.FRIDAY, DAYS_OF_WEEK.SATURDAY]
  public event: CalendarEvent = {
      start: this.date,
      title: 'Event!',
      color: this.colors.red
  }
  public event2:CalendarEvent = {
    start: this.date,
    title:"Probando men",
    color:this.colors.red
  }
  public calEvents: CalendarEvent[] = [];
  activeDayIsOpen: boolean = false;
  constructor(
    public navCtrl: NavController, 
    private newsService:NewsService,
    public navParams: NavParams) {

    let fecha = this.date.toISOString().split("T")[0].split("-");
    this.ano = fecha[0];
    this.mes = fecha[1];
    //alert("Mes: "+this.mes)
    //alert("Año: "+this.ano) 
    this.ionViewDidLoad()
  }

  // beforeMonthViewRender():void{
  //   setTimeout(()=>{
  //     // console.log("beforeMonthViewRender")
      
  //     let row_header = document.querySelectorAll(".cal-cell-row.cal-header > div")
  //     // console.log(row_header)
  //     for(var i=0;i<row_header.length;i++){
  //       // console.log(row_header[i].innerHTML)
  //       row_header[i].innerHTML =  row_header[i].innerHTML.trim()[0]
  //     }
  //   },100)
  // }
  ionViewDidLoad() {
    const getStart: any = {
      month: startOfMonth,
      week: startOfWeek,
      day: startOfDay
    }[this.view];

    const getEnd: any = {
      month: endOfMonth,
      week: endOfWeek,
      day: endOfDay
    }[this.view];

    this.events$ = this.newsService.getNewsDate(this.ano,this.mes).map((results) => {
      console.log(results);
      
      return results.map((notice: Notice) => {
        //alert("Aqui: "+notice.title)
        //console.log(new Date(notice.date.replace(/-/g, "/")))
        return {
          title: notice.title,
          start: new Date(notice.date.replace(/-/g, "/")),
          color: this.colors.yellow,
          meta: {
            notice
          }
        };
      });
    });
    this.events$.subscribe(()=>{
      console.log("Termino")
    })
  }
  dayClicked({
    date,
    events
  }: {
    date: Date;
    events: Array<CalendarEvent<{ notice: Notice }>>;
  }): void {
    if (isSameMonth(date, this.viewDate)) {
      if (
        (isSameDay(this.viewDate, date) && this.activeDayIsOpen === true) ||
        events.length === 0
      ) {
        this.activeDayIsOpen = false;
      } else {
        this.activeDayIsOpen = true;
        this.viewDate = date;
      }
    }
  }

  eventClicked(event: CalendarEvent<{ notice: Notice }>): void {
    this.navCtrl.push(DetailPage, {
      id: event.meta.notice.id
    });
  }
}



