import { Component } from '@angular/core';
import { NavController,ViewController, NavParams } from 'ionic-angular';

@Component({
  selector: 'page-video',
  templateUrl: 'video.html',
})
export class VideoPage {
  public link:String;
  constructor(
    public viewCtrl: ViewController,
    public navCtrl: NavController,
    public navParams: NavParams) {
      this.link = "";
  }

  ionViewDidLoad() {
    let element = document.getElementById("video-popover")
    let element2 = document.querySelector("#video-popover>.scroll-content")
    element2['style']['padding-bottom'] = "0!important"
    let parent=element.parentElement
    let parent2 = parent.parentElement
    //parent2.parentElement.style['height'] = "fit-content"
  }
  add(){
    this.viewCtrl.dismiss(this.link)
  }

}



