import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import {MessagePage} from '../message/message';
import {UsersPermisionsService} from '../../services/permisions';
import {UserService} from '../../services/user';

@Component({
  selector: 'page-users-chat',
  templateUrl: 'users-chat.html',
})
export class UsersChatPage {
  users:Array<{
    id:number,
    permision:number,
    email:string,
    nombre:string,
    usuario:string,
    privilegio:string
  }>
  userData:any;
  constructor(
    public navCtrl: NavController,
    private usersPermisionsService:UsersPermisionsService,
    private userService:UserService,
    public navParams: NavParams
  ) {
    this.userData = this.userService.getData()
    this.users = []
  }

  ionViewDidLoad() {
    let privilege = this.userData.user.userdata.admin
    
    console.log("Privilegio: ",privilege)
    if(privilege == 3){
      this.admins()
    }else if(privilege == 1){
      this.usersMessages() 
    }
  }

  admins(){
    this.users = []
    this.usersPermisionsService.admins().subscribe((data)=>{
      if(data){
        data.forEach(user => {
          this.users.push(user)
        });
      }
    })
  }
  usersMessages(){
    this.users = []
    this.usersPermisionsService.usersMessages().subscribe((data)=>{
      if(data){
        data.forEach(user => {
          this.users.push(user)
        })
      }
    })
  }
  contact(user:any){
    this.navCtrl.push(MessagePage, {
      user: user,
      type:"normal"
    });
  }
}



