import { Component } from '@angular/core';
import { NavController,ViewController,ToastController, NavParams } from 'ionic-angular';
import {MessagesCommentsService} from "../../services/messages-comments"
import {CommentsService} from "../../services/comments";
import { UserService } from '../../services/user';
@Component({
  selector: 'page-subcomments',
  templateUrl: 'subcomments.html',
})
export class SubcommentsPage {
  comment_id:any;
  subcomments:any;
  comment:string;
  user_id:number
  admin:any
  constructor(
    public navCtrl: NavController,
    public messagesCommentsService:MessagesCommentsService,
    public commentsService:CommentsService,
    public viewCtrl: ViewController,
    public toastCtrl: ToastController,
    private userService: UserService,
    public navParams: NavParams) {
      this.comment_id = this.navParams.get("comment").id
      this.user_id = this.userService.getData().user.userdata.id;
      console.log("mi id: ",this.user_id)
      this.subcomments = []
      // this.admin = this.navParams.get('user').usuario;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad SubcommentsPage');
    let element = document.querySelector(".modal-wrapper")
    element['style']['margin'] = 0
    element['style']['top'] = 0
    element['style']['left'] = 0
    element['style']['width'] = '100%'
    element['style']['height'] = '100%'
    this.getComments()
  }
  back(){
    this.viewCtrl.dismiss()
  }
  send(message:string){
    let privilege = this.userService.getData().user.userdata.admin
    if (privilege == 1 || privilege == 3) {
        let body = 'data=' + JSON.stringify({
            comentario: this.comment_id,
            contenido: message
        });

        if (message.length > 0) {
            this.commentsService.addSub(body).subscribe(data => {
                if (data) {
                    let toast = this.toastCtrl.create({
                        message: data.msg,
                        duration: 2000
                    });
                    toast.present();
                    console.log(data)
                    if (data.status == "ok") {
                        this.comment = ""
                        // this.events.publish('comment:new', '')
                    }
                }
            })
        }
    }
  }
  getComments(){
    this.messagesCommentsService.getSubComments(this.comment_id).subscribe((data)=>{
      if(data){
        this.subcomments = data
      }
    })
  }
}



