import { Component } from '@angular/core';
import { AlertController,NavController,ToastController, NavParams } from 'ionic-angular';

import {LoginPage} from '../login/login';

import {RegisterService} from '../../services/register';
import {UserService} from '../../services/user';
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  public user = {
    name:'',
    mail:'',
    pwd:'',
    pwdConfirm:''
  }
  constructor(
    private alertCtrl:AlertController,
    private toastCtrl:ToastController,
    private registerService:RegisterService,
    private userService:UserService,
    public navCtrl: NavController,
    public navParams: NavParams) {

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RegisterPage');
  }

  register(){

    if(this.user.pwd.length  < 1 || this.user.pwdConfirm.length < 1 ){
      let alert = this.alertCtrl.create({
          title: 'Campo requerido',
          subTitle: 'Contraseña requerida',
          buttons: ['Volver']
        });
        alert.present();
      return
    }
    if(this.user.pwd != this.user.pwdConfirm){
      let alert = this.alertCtrl.create({
          title: 'Error',
          subTitle: 'Las contraseñas no coinciden intente nuevamente',
          buttons: ['Volver']
        });
      this.user.pwdConfirm = ''
        alert.present();
      return
    }

    let body = 'data='+ JSON.stringify({
      usuario: this.user.name,
      email: this.user.mail,
      nombre: this.user.name,
      contrasena: this.user.pwd,
      contrasena2: this.user.pwdConfirm,
      idRegistration: ''+this.userService.getIdRegistration()
    });
    this.registerService.register(body).subscribe((data)=>{
      if(data.msg =="ok"){
          let toast = this.toastCtrl.create({
            message: 'Usuario registrado correctamente',
            duration: 2000
          });
          toast.present();
        this.navCtrl.setRoot(LoginPage)
      }else if(data.msg =="error"){
        // console.log(data.data)
        let toast = this.toastCtrl.create({
         message: data.data,
         duration: 2000
         });
         toast.present();
      }
    })
  }
}



