import { Component } from '@angular/core';
import { NavController,ViewController, NavParams } from 'ionic-angular';

@Component({
	selector: 'page-paragraph-format',
	templateUrl: 'paragraph-format.html',
})
export class ParagraphFormatPage {

	formats:Array<{
		name:String,
		value:String
	}>
	constructor(
		public navCtrl: NavController,
		public viewCtrl:ViewController,
		public navParams: NavParams) {

		this.formats = [
			{
				'name':'Normal',
				'value':'p'
			},
			{
				'name':'Titulo 1',
				'value':'H1'
			},
			{
				'name':'Titulo 2',
				'value':'H2'
			},
			{
				'name':'Titulo 3',
				'value':'H3'
			},
			{
				'name':'Titulo 4',
				'value':'H4'
			},
			{
				'name':'Titulo 5',
				'value':'H5'
			},
			{
				'name':'Titulo 6',
				'value':'H6'
			},
			{
				'name':'Codigo',
				'value':'pre'
			},
		]
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad ParagraphFormatPage');
	}
	setFormat(format){
		this.viewCtrl.dismiss(format)
	}
}



