import { Component } from '@angular/core';
import { Events, ToastController, NavController, NavParams } from 'ionic-angular';
import { Http, Headers, RequestOptions } from '@angular/http';

import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

import { NewsService } from '../../services/news';
import { UserService } from '../../services/user';
@Component({
    selector: 'page-detail',
    templateUrl: 'detail.html',
})
export class DetailPage {
    http: Http;
    userData: any;
    headers: Headers;
    ip: any;
    toastCtrl: ToastController;
    user_name:string;
    noticia: {
        id: number,
        title: string,
        body: SafeHtml,
        cover: string,
        autor: string,
        date: string,
        tags: string
    };
    comments: Array<{
        id: number,
        comentario: string,
        usuario: string,
        date: string,
        subcomentarios:any
    }>
    public comment = ""
    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        private newsService: NewsService,
        public userService: UserService,
        private events: Events,
        http: Http,
        toastCtrl: ToastController,
        private sanitizer: DomSanitizer
    ) {
        // alert(JSON.stringify(this.navParams.get("noticia")))
        this.http = http;
        this.userData = this.userService.getData()
        this.user_name = this.userData.user.userdata.usuario
        this.toastCtrl = toastCtrl
        this.noticia = {
            id: 0,
            title: '',
            body: '',
            cover: '',
            autor: '',
            date: '',
            tags: ''
        }
        this.comments = []
        let id = this.navParams.get("id");
        this.newsService.get(id).subscribe(data => {
            if (data) {
                this.noticia.id = data.id
                this.noticia.title = data.title
                this.noticia.body = data.body.replace("covers/", "covers%2F").replace("magueitoramp;", "&")
                this.noticia.cover = this.sanitizer.bypassSecurityTrustUrl("https://firebasestorage.googleapis.com/v0/b/interfresas-8627e.appspot.com/o/covers" + data.cover.replace("mague1", "%252F").replace("mague2", "?alt=").replace("mague3", "&token="))['changingThisBreaksApplicationSecurity'].replace('covers/', 'covers%2F')
                this.noticia.autor = data.autor
                this.noticia.date = data.date
                this.noticia.tags = data.tags
                // console.log(this.noticia.body)
                this.events.publish('comment:new', '')
            }
        });
    }
    //// "host":"http://ondesarrollo.com/proyectos/appinterfresa/"
    ionViewDidLoad() {
        this.getComments(this.noticia.id)
        this.events.subscribe('comment:new', () => {
            this.getComments(this.noticia.id)
        })
    }
    addFav() {

        let body = 'data=' + JSON.stringify({
            id: this.noticia.id

        });
        this.newsService.addFav(body).subscribe(data => {
            if (data) {
                let toast = this.toastCtrl.create({
                    message: data.msg,
                    duration: 2000
                });
                toast.present();
                this.events.publish('comment:new', '')
            }
        });
    }

    addComment() {
        let privilege = this.userData.user.userdata.admin
        if (privilege == 1 || privilege == 3) {
            let body = 'data=' + JSON.stringify({
                noticia: this.noticia.id,
                comentario: this.comment
            });

            if (this.comment.length > 0) {
                this.newsService.addComment(body).subscribe(data => {
                    if (data) {
                        let toast = this.toastCtrl.create({
                            message: data.msg,
                            duration: 2000
                        });
                        toast.present();
                        console.log(data)
                        if (data.status == "ok") {
                            this.comment = ""
                            this.events.publish('comment:new', '')
                        }
                    }
                })
            }
        }
    }

    getComments(id: number) {
        this.comments = []
        this.newsService.comments(id).subscribe((data) => {
            let news_comments = new Array()
            data.forEach(comment => {
                news_comments.push({
                    id: comment.id,
                    comentario: comment.comentario,
                    usuario: comment.usuario,
                    date: comment.date,
                    subcomentarios:comment.subcomentarios
                })
            });
            this.comments = news_comments

        })
    }
}



