import { Component,ViewChild } from '@angular/core';
import { Events,Platform,Nav,MenuController,ToastController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
//import { Push, PushObject, PushOptions } from '@ionic-native/push';
import { FCM } from '@ionic-native/fcm';

import { AppPreferences } from '@ionic-native/app-preferences';

import firebase from 'firebase';

//Pages
import {LoginPage} from '../pages/login/login';
import { WelcomePage } from '../pages/welcome/welcome';
import { HomePage } from '../pages/home/home';
import { WysiwygPage } from '../pages/wysiwyg/wysiwyg';
import {UserService} from '../services/user';
import {CalendarPage} from '../pages/calendar/calendar';
import {UsersPage} from '../pages/users/users';
import {RegisterPage} from '../pages/register/register';
import {MessagePage} from '../pages/message/message';
import { UsersChatPage } from "../pages/users-chat/users-chat";
import { CommentsChatPage } from "../pages/comments-chat/comments-chat";
import { CategoriesPage } from '../pages/categories/categories';
import { SectionsPage } from '../pages/sections/sections';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) navCtrl: Nav;

  rootPage:any = WelcomePage;
  userData: any;
  // pages: Array<{ title: string, component: any }>;
  pages:any
  constructor(
    public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    private menuCtrl:MenuController,
    private toastCtrl:ToastController,
    public events:Events,
    //public push:Push,
    public fcm:FCM,
    public appPreferences:AppPreferences,
    private userService:UserService) {
    platform.ready().then(() => {
      this.initializeApp();
      this.ionViewDidLoad();
      
      this.pages = [
        
      ];

      const firebaseConfig = {
        apiKey: "AIzaSyDN8MYCqVgBAa5tiUpL1N8VUm4g68GtpCg",
        authDomain: "https://interfresas-8627e.firebaseio.com",
        storageBucket: "interfresas-8627e.appspot.com",
        // messagingSenderId: "<message_sender_id>"

      };

      firebase.initializeApp(firebaseConfig);

    });
  }
  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      //this.splashScreen.hide();
      
      this.pushsetup();
      setTimeout(() => {
        this.splashScreen.hide();
      }, 100);
    });
  }

  pushsetup() {
      
          /*const options: PushOptions = {
          
           ios: {
              alert: 'true',
              badge: true,
              sound: 'false'
           },
           windows: {}
          };
          
          const pushObject: PushObject = this.push.init(options);
          
          pushObject.on('notification').subscribe((notification: any) => {
          if (notification.additionalData.foreground) {
            if(notification.title == "Cierre de sesion"){
              this.closeSession()
            }
            let toas = this.toastCtrl.create({
              message:notification.message,
              duration:3000
            })
          }
          });
          
          pushObject.on('registration').subscribe((registration: any) => {
           let idRegistration = registration.registrationId
           this.userService.setIdRegistration(idRegistration)
          });
          
          pushObject.on('error').subscribe(error => alert('Error with Push plugin' + error));
          */
          this.fcm.subscribeToTopic('marketing');
  
        this.fcm.getToken().then(token=>{
          console.log("Id Registration IOS:");
          console.log(token);
          let idRegistration = token
          this.userService.setIdRegistration(idRegistration)
          
        },error=>{
          console.log("error");
          
          if(error){
            console.log("error: ",error)
          }
        })
        this.fcm.onNotification().subscribe(data=>{
          if(data.wasTapped){
            console.log("Received in background");
          } else {
            console.log("Received in foreground");
          };
        })
        
        this.fcm.onTokenRefresh().subscribe(token=>{
          console.log(token);
        })

  }

  //Eventos
  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.navCtrl.setRoot(page.component,{section:page.id,sectionName:page.nombre});
  }
  closeSession(){
    this.appPreferences.store("user","")
    this.userService.deleteData()
    this.navCtrl.setRoot(LoginPage)
    this.menuCtrl.enable(false, 'menu');
  }
  ionViewDidLoad() {
    this.menuCtrl.enable(false, 'menu');
    this.listenLoginEvents();
  }
  
  menuClosed() {
    this.events.publish('menu:closed', '');
  }

  menuOpened() {
    this.events.publish('menu:opened', '');
  }

  listenLoginEvents() {
    this.events.subscribe('user:login', () => {
      this.userData = this.userService.getData();
      console.log("Datos de usuario: ",this.userData)
      
      if(this.userData.user.userdata.admin == 1){
        this.pages =  [
          { title: 'Inicio', component: HomePage } 
        ]
        this.userData.user.userdata.sections.forEach(section => {
          this.pages.push({
            component: SectionsPage,
            title:section.nombre,
            id:section.id,
            nombre:section.nombre
          })
        });
        this.pages.push(
          { title: 'Calendario', component: CalendarPage,id:0 },
          { title: 'Cargar Noticia', component: WysiwygPage,id:0 },
          { title: 'Agregar Usuarios', component: RegisterPage,id:0 },
          { title: 'Comentarios', component: CommentsChatPage,id:0 },
          { title: 'Soporte', component: UsersChatPage,id:0 },
          { title: 'Privilegios de Usuarios', component: UsersPage,id:0 },
        )
      }else if(this.userData.user.userdata.admin == 2){
        this.pages =  [
          { title: 'Inicio', component: HomePage },
          { title: 'Calendario', component: CalendarPage },
        ]
      }else{
        this.pages =  [
          { title: 'Inicio', component: HomePage },
          { title: 'Calendario', component: CalendarPage },
          { title: 'Contactar con administrador', component: UsersChatPage },
        ]
      }
      this.menuCtrl.enable(true, 'menu');
    });

    this.events.subscribe('user:signup', () => {
      //this.enableMenu(true);
    });

    this.events.subscribe('user:logout', () => {
      //this.enableMenu(false);
      // alert("Cerrando sesion")
    });
  }
}




