import { Component } from '@angular/core';
import { AlertController,NavController, NavParams } from 'ionic-angular';

import {HomePage} from '../home/home';
import {RegisterPage} from '../register/register';

//Services
import {LoginService} from '../../services/login';
import {UserService} from '../../services/user';

import { AppPreferences } from '@ionic-native/app-preferences';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  public user = {
    name:'',
    pwd:''
  }
  constructor(
    private loginService:LoginService,
    private userService:UserService,
    private alertCtrl: AlertController,
    public navCtrl: NavController,
    public appPreferences:AppPreferences,
    public navParams: NavParams) {
      
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }
  login(){
    let body = 'data='+ JSON.stringify({
      usuario: this.user.name,
      contrasena: this.user.pwd,
      idRegistration: ''+this.userService.getIdRegistration()
    });
    this.loginService.login(body).subscribe((data)=>{
      if(data.token){
          // console.log(data)
          this.appPreferences.remove('user')
          this.appPreferences.store("user",data)
          this.userService.addUserData(data);
          this.navCtrl.setRoot(HomePage);
        }
        else{
          let alert = this.alertCtrl.create({
              title: 'Error',
              subTitle: 'Usuario o contraseña invalidos',
              buttons: ['Volver']
            });
            alert.present();
          return
        }
    })
    // this.navCtrl.setRoot(HomePage)
  }
  register(){
    this.navCtrl.setRoot(RegisterPage)
  }
}



