import { Component } from '@angular/core';
import { NavController,ViewController, NavParams } from 'ionic-angular';
import { Camera,CameraOptions } from '@ionic-native/camera';

@Component({
  selector: 'page-rezise-image',
  templateUrl: 'rezise-image.html',
})
export class ReziseImagePage {
  base64:any;
  imageData:any;
  public height:any
  public width:any
  constructor(
    public viewCtrl: ViewController,
    public navCtrl: NavController,
    private camera:Camera,
    public navParams: NavParams) {
    this.width = ''
    this.height = ''
  }

  ionViewDidLoad() {
    let element = document.querySelector("#rezise-image>.scroll-content")
    element['style']['padding-bottom'] = "0!important"
    // element['style']['position'] = 'absolute'
    // element['style']['top'] = 0
    // element['style']['left'] = 0
    // element['style']['display'] = 'block'
    // element['style']['width'] = '100%'
    // element['style']['height'] = '100%'
    // element['style']['margin'] = 0
    let element2 = document.getElementById("rezise-image")

    let parent=element2.parentElement
    let parent2 = parent.parentElement
    parent2.parentElement.style['height'] = "fit-content"
  }

  getImage(){
    let options: CameraOptions = {
      quality: 100,
      targetWidth:100,
      targetHeight:100,
      sourceType:this.camera.PictureSourceType.PHOTOLIBRARY,
      destinationType:this.camera.DestinationType.DATA_URL
    }
    if(this.width.length>0){
      options.targetWidth = parseInt(this.width)
    }
    if(this.height.length>0){
      options.targetHeight = parseInt(this.height)
    }
    this.camera.getPicture(options).then((img)=>{
      if(img){
        this.base64 = img
        this.viewCtrl.dismiss(img)
        // alert(document.getElementById("editor").innerHTML)
      }
    },(err)=>{
      alert(err)
      console.log(err)
    })
  }

}



