import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import {MessageCommentsPage} from '../message-comments/message-comments';
import {CommentsService} from '../../services/comments';
import {UserService} from '../../services/user';

@Component({
  selector: 'page-comments-chat',
  templateUrl: 'comments-chat.html',
})
export class CommentsChatPage {
  comments:Array<{
    id:number,
    title:number,
    comments:number,
    comments_viewed:number
  }>
  userData:any;
  constructor(
    public navCtrl: NavController,
    private commentsService:CommentsService,
    private userService:UserService,
    public navParams: NavParams
  ) {
    this.userData = this.userService.getData()
    this.comments = []
  }

  ionViewDidLoad() {
    let privilege = this.userData.user.userdata.admin
    
    console.log("Privilegio: ",privilege)
    if(privilege == 3){
      this.admins()
    }else if(privilege == 1){
      this.usersMessages() 
    }
  }

  admins(){
    this.comments = []
    this.commentsService.notices().subscribe((data)=>{
      if(data){
        data.forEach(user => {
          this.comments.push(user)
        });
      }
    })
  }
  usersMessages(){
    this.comments = []
    this.commentsService.noticesComments().subscribe((data)=>{
      if(data){
        data.forEach(user => {
          this.comments.push(user)
        })
      }
    })
  }
  comments_list(notice:any){
    this.navCtrl.push(MessageCommentsPage, {
      notice_id: notice.id,
      notice_title: notice.title,
      type:"normal"
    });
  }
}



