import { Component,Input,Output,EventEmitter } from '@angular/core';
import { AlertController,NavController, NavParams,Platform } from 'ionic-angular';
import {AppPreferences} from "@ionic-native/app-preferences"
import {LoginPage} from '../login/login';
import {HomePage} from '../home/home';
import {RegisterPage} from '../register/register';

import {LoginService} from "../../services/login";
import {UserService} from "../../services/user";

@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html',
})
export class WelcomePage {
  constructor(
    public alertCtrl: AlertController, 
    public navCtrl: NavController, 
    public navParams: NavParams,
    private platform:Platform,
    private loginService:LoginService,
    private userService:UserService,
    private appPreferences:AppPreferences
  ) {
    this.platform.ready().then(()=>{
      this.appPreferences.fetch('user').then((res)=>{
        if((typeof res)=='object'){
          this.userService.addUserData(res)
          this.navCtrl.setRoot(HomePage)
        }
      },(error)=>{
        
      })
    })
  }

  ionViewDidLoad() {

  }

  login(){
    this.navCtrl.setRoot(LoginPage)
  }
  invited(){
    let body = 'data='+ JSON.stringify({
      invite:true,
      idRegistration: ''+this.userService.getIdRegistration()
    });
    this.loginService.invited(body).subscribe((data)=>{
      if(data.token){
          // console.log(data)
          this.appPreferences.remove('user')
          this.appPreferences.store("user",data)
          this.userService.addUserData(data);
          this.navCtrl.setRoot(HomePage);
        }
        else{
          let alert = this.alertCtrl.create({
              title: 'Error',
              subTitle: 'Error al ingresar como invitado',
              buttons: ['Volver']
            });
            alert.present();
          return
        }
    })
  }

}



